.multiselected {

    .operation-container {
        border: var(--floating-label-size) solid #ff4081;

    }

}

.params {
    display: flex;
    flex-direction: row;
    padding: 8px 16px;
    width: 100%;
    gap: 16px;
    flex: 1 0 auto;

}


.operation-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: max-content;
    min-width: 600px;
    border-radius: 8px;
    color: white;


    .outlet_row {
        display: flex;
        flex-direction: row;
        color: blue;
        position: relative;
        top: -24px;
        left: 16px;
    }



}


.black-overlay {

    width: 100%;
    height: 100%;


    &.recomputing {
        background-color: black;
        opacity: 1;
    }

    &.stable {
        animation: fadeToBlack 0.5s ease-out reverse;
    }

}

@keyframes fadeToBlack {
    0% {
        opacity: 1;
        /* Fully transparent at the start */
        background-color: transparent;
    }

    100% {
        opacity: 1;
        background-color: black;
        /* Fully opaque  at the end */
    }
}







.errorWarning {
    position: absolute;
    top: -75px;
    left: 300px;
    width: 1200px;
    background-color: red;
    border: 2px solid black;
    border-radius: 40px;
    z-index: 100;
    color: white;
    padding: 10px;


    button {
        font-size: var(--scalable-text-heading-size);
        height: max-content;
    }

}

.operation-parent {
    position: absolute;
    width: max-content;
    height: max-content;


}



.mat-mdc-form-field-hint,
.mat-mdc-form-field-subscript-wrapper {
    font-size: 14px;
}

.top-row {
    display: flex;
    flex-direction: row;
    vertical-align: bottom;
    justify-content: flex-start;
    padding: 20px;

}




