button.is-primary {
    background-color: transparent;
    border-color: transparent;
    color: #fff;
}

.cxn-row {
    display: flex;
    flex-direction: row;
    text-align: left;
    gap: 16px;
    align-items: center;
    overflow: visible;
    // height: 24px;
    padding: 8px 8px 0px 8px;
    //background:rgba(255, 255, 255, 0.5)




}

.hidden {
    display: none;
}

.cxn-row input {
    border: none;
    background: #00d1b2;
    color: white;
}


.input-color {
    width: 30px;
    height: 30px;
    border: thin solid white;
}

button.input {
    position: relative;
    background-color: #00d1b2;
    color: white;
    border: none;
    width: 30px;
}

button.cxn {
    background-color: #00d1b2;
    border: none;
    border-left: thin solid white;
    color: white;
}

.input-name {
    margin-left: 8px;
    margin-right: 24px;
    line-height: 30px;
}


.inputs {
    display: flex;
    justify-content: flex-start;
    text-align: center;
}



.input-group {
    display: flex;
    flex-direction: row;
    margin-right: 8px;
}

.input-group input {
    width: 35px;
}



button.is-primary.is-active {
    background-color: rgb(255, 64, 129);
}

.help {
    background-color: #333333;
    color: white;
    height: 20px;
    border: none;
}

.help:hover {
    color: white;
}

.name {
    color: white;
    font-size: var(--scalable-text-heading-size);

    font-weight: bold;
}

.hidden {
    display: none;
}



.operation-details {
    animation-name: added_to_workspace;
    animation-duration: 4s;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 8px;
    align-items: center;

    .mat-mdc-icon-button {
        font-size: 14px;
    }
}

.outputs {
    display: flex;
    flex-direction: row;
}

.draftrendering {
    width: 100%;
}